import { ToastContainer, Flip } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import { Game } from "./components/Game";
import React, { useEffect, useMemo, useState } from "react";
import { Infos } from "./components/panels/Infos";
import { useTranslation } from "react-i18next";
// import { InfosCo } from "./components/panels/InfosCo";
// import { InfosFr } from "./components/panels/InfosFr";
// import { InfosHu } from "./components/panels/InfosHu";
// import { InfosNl } from "./components/panels/InfosNl";
// import { InfosPl } from "./components/panels/InfosPl";
// import { InfosDe } from "./components/panels/InfosDe";
// import { InfosJa } from "./components/panels/InfosJa";
import { Settings } from "./components/panels/Settings";
import { useSettings } from "./hooks/useSettings";
import { Reservoirdle } from "./components/Reservoirdle";
import { Stats } from "./components/panels/Stats";
import { Twemoji } from "react-emoji-render";
import { getDayString, useTodays } from "./hooks/useTodays";

const supportLink: Record<string, string> = {
  UA: "https://donate.redcrossredcrescent.org/ua/donate/~my-donation?_cv=1",
};

export default function App() {
  const { t, i18n } = useTranslation();

  const dayString = useMemo(getDayString, []);
  const [{ lake }] = useTodays(dayString);

  const [infoOpen, setInfoOpen] = useState(false);
  const [settingsOpen, setSettingsOpen] = useState(false);
  const [statsOpen, setStatsOpen] = useState(false);

  const [settingsData, updateSettings] = useSettings();

  useEffect(() => {
    if (settingsData.theme === "dark") {
      document.documentElement.classList.add("dark");
    } else {
      document.documentElement.classList.remove("dark");
    }
  }, [settingsData.theme]);

  const InfosComponent = Infos;

  return (
    <>
      <ToastContainer
        hideProgressBar
        position="top-center"
        transition={Flip}
        theme={settingsData.theme}
        autoClose={2000}
        bodyClassName="font-bold text-center"
        toastClassName="flex justify-center m-2 max-w-full"
        style={{ width: 500, maxWidth: "100%" }}
      />
      <InfosComponent
        isOpen={infoOpen}
        close={() => setInfoOpen(false)}
        settingsData={settingsData}
      />
      <Settings
        isOpen={settingsOpen}
        close={() => setSettingsOpen(false)}
        settingsData={settingsData}
        updateSettings={updateSettings}
      />
      <Stats
        isOpen={statsOpen}
        close={() => setStatsOpen(false)}
        distanceUnit={settingsData.distanceUnit}
      />
      <div className="flex justify-center flex-auto dark:bg-slate-900 dark:text-slate-50">
        <div className="w-full max-w-lg flex flex-col">
          <header className="border-b-2 px-3 border-gray-200 flex">
            <button
              className="mr-3 text-xl"
              type="button"
              onClick={() => setInfoOpen(true)}
            >
              <Twemoji text="❓" />
            </button>
            <h1 className="text-4xl font-bold uppercase tracking-wide text-center my-1 flex-auto">
              <span className="text-blue-600">Reservoir</span>dle
            </h1>
            <button
              className="ml-3 text-xl"
              type="button"
              onClick={() => setStatsOpen(true)}
            >
              <Twemoji text="📈" />
            </button>
            <button
              className="ml-3 text-xl"
              type="button"
              onClick={() => setSettingsOpen(true)}
            >
              <Twemoji text="⚙️" />
            </button>
          </header>
          <Game settingsData={settingsData} updateSettings={updateSettings} />
          <div className="flex justify-center m-6">
            <div className="flex flex-col m-2">
              <p className="text-sm text-center">
                National Hydrography Dataset:{" "}
                <a
                  className="underline pl-1"
                  href="https://data.cnra.ca.gov/dataset/national-hydrography-dataset-nhd/resource/ec84510b-2cd3-41c1-9f3e-849209582a72?inner_span=True"
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  California Lakes and Reservoirs
                </a>{" "}
              </p>
            </div>
          </div>

          <footer className="flex justify-center items-center mt-8 mb-4">
            <Twemoji
              text="❤️"
              className="flex items-center justify-center mr-1"
            />{" "}
            <Reservoirdle />?
            {lake && supportLink[lake.code] != null ? (
              <a
                className="underline pl-1"
                href={supportLink[lake.code]}
                target="_blank"
                rel="noopener noreferrer"
              >
                <div className="w-max">{t(`support.${lake.code}`)}</div>
              </a>
            ) : (
              <a
                className="underline pl-1"
                href="https://worldle.teuteuf.fr/"
                target="_blank"
                rel="noopener noreferrer"
              >
                <div className="w-max">
                  <Twemoji svg text="try WORLDLE" />
                </div>
              </a>
            )}
          </footer>
        </div>
      </div>
    </>
  );
}
